import React, {useEffect, useState} from 'react';
import axios from "axios";

const List = () => {
    const [newsList, setNewsList] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const apiKey = 'PwA3g7rg7brtYe29mPQr';
                const secretApiKey = 'MkU0LOXXvm';
                const apiUrl = 'https://openapi.naver.com/v1/search/news.json';
                const query = '코로나'; // 기본 검색어 설정
                const response = await axios.post(`${apiUrl}?query=${query}`,
                    {},{
                        'X-Naver-Client-Id': apiKey,
                        'X-Naver-Client-Secret': secretApiKey
                    }
                ).then(res => {setNewsList(res.data.items)});
            } catch (error) {
                console.error('Error fetching news:', error);
            }
        };

        fetchData();
    }, []); // 빈 배열을 전달하여 컴포넌트가 마운트될 때만 호출

    return (
        <ul>
            {newsList.map((news, index) => (
                <li key={index}>
                    <a href={news.link} target="_blank" rel="noopener noreferrer">
                        {news.title}
                    </a>
                </li>
            ))}
        </ul>
    );
};

export default List;
